export default {
	name: '',
	components: {},
	props: {},
	data() {
		return {
			username: '',
			id: ''
		};
	},
	methods: {
		// 去登录注册页面
		goRegisterLogin() {
			uni.navigateTo({
				url: '/pages/registerlogin/registerlogin',
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		},
		// 去我的资料
		goMyDetail() {
			uni.navigateTo({
				url: '/pages/mydetail/mydetail',
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		}
	},
	mounted() {
	},
	onLoad() {
		// this.username = this.$store.state.username;
	},
	onShow() {
		let user = uni.getStorageSync('userInfo');
		let username = uni.getStorageSync('username');
		this.id = user.id;
		this.username = username
	},
	filters: {},
	computed: {},
	watch: {},
	directives: {}
};
